import styled from "styled-components"
import logoPic from "../../statics/logo.png"
export const HeaderWrapper = styled.div`
    z-index: 1;
	position: relative;
	height: 56px;
	border-bottom: 1px solid #f0f0f0;
`
export  const Logo = styled.div`
    position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100px;
	height: 56px;
	background: url(${logoPic});
	background-size: contain; 
	cursor:pointer;
`
//设置导航条
export const Nav = styled.div`
	width: 960px;
	height: 100%;
 
	padding-right : 70px;
	box-sizing : border-box;
	margin: 0 auto;
`
//设置导航条里的内容
export const NavItem = styled.div`
	line-height : 56px;
	padding : 0 15px;
	color: #333;
	font-size : 17px;
	cursor : pointer;
/* &.left表示left在自身的身上的属性 */
	&.left{
		float: left; 
	};
	&.right {
		float : right;
	}
	&.active {
		color: #ea6f5a;
	};

`;
//编写导航条里的搜索框
export const SearchWrapper = styled.div`
	position :relative;
 
	float : left;
	.zoom {
		position: absolute;
		right: 5px;
		bottom: 5px;
		width: 30px;
		line-height: 30px;
		border-radius: 15px;
		text-align: center;
		&.focused {
			background: #777;
			color: #fff;
		}
	}
`
export const SearchNav = styled.input.attrs({
	placeholder : '搜索'
})`
	width: 160px;
	height: 38px;
	padding: 0 30px 0 20px;
	margin-top: 9px;
	margin-left: 20px;
	border-radius : 19px;
	border : 1px solid #333;
	/* 点击时没有框出现 */
	outline : none; 
	box-sizing : border-box;
	background: #eee;
	font-size: 14px;
	color: #666;
	&::placeholder {
		color: #999;
	}
	&.focused{
		width : 200px;
	}
	/* 进行动画的四步 */
	/* 这里时间.2s要和前面一致 timeout={200}*/
	&.slide-enter {
		transition : all .2s ease-in-out;
	}
	&.slide-enter-active {
		width : 200px;
	}
	&.slide-exit {
		transition : all .2s ease-in-out;
	}
	&.slide-exit-active {
		width: 160px;
	}
`
export const SearchInfo = styled.div`
	position: absolute;
	left: 0;
	top: 56px;
	width: 240px;
	padding: 0 20px;
	box-shadow: 0 0 8px rgba(0, 0, 0, .2);
	background: #fff;
`;

export const SearchInfoTitle = styled.div`
	margin-top: 20px;
	margin-bottom: 15px;
	line-height: 20px;
	font-size: 14px;
	color: #969696;
`;

export const SearchInfoSwitch = styled.span`
	float: right;
	font-size: 13px;
	cursor: pointer;
	.spin {
		display: block;
		float: left;
		font-size: 12px;
		margin-right: 2px;
		transition: all .2s ease-in;
		transform-origin: center center;
	}
`;

export const SearchInfoList = styled.div`
	overflow: hidden;
`;

export const SearchInfoItem = styled.a`
	display: block;
	float: left;
	line-height: 20px;
	padding: 0 5px;
	margin-right: 10px;
	margin-bottom: 15px;
	font-size: 12px;
	border: 1px solid #ddd;
	color: #787878;
	border-radius: 3px;
`;

//编辑右边的 注册与写文章
export const Addition = styled.div`
	position:absolute;
	right: 0px;
	top : 0px;
	height: 56px;
`;	
export const Button = styled.div`
	float: right;
	margin-top: 9px;
	margin-right: 20px;
	padding: 0 20px;
	line-height: 38px;
	border-radius: 19px;
	border: 1px solid #ec6149;
	font-size: 14px;
	&.writtig{
		color: #fff;
		background: #ec6149;
	}
	&.reg{
		color: #ec6149;
	};
	cursor : pointer
`
 



